import React from "react";
import ReactDOM from "react-dom";

const mouseHandler = (canDrag=false, cursor=undefined)=> (WrappedComponent)=> {

    return class extends WrappedComponent {

        state = {
            isOver: false
        };

        isDown = false;
        localX = 0;
        localY = 0;
        globalX = 0;
        globalY = 0;
        dx = 0;
        dy = 0;

        cursor = cursor;
        canDrag = canDrag;

        // 给外部的回调
        onMouseOver = undefined;
        onMouseDown = undefined;
        onMouseOut = undefined;
        onMouseUp = undefined;
        onMouseMove = undefined;
        onClick = undefined;
        onDragStart = undefined;
        onDrag = undefined;
        onDragEnd = undefined;

        constructor(props) {
            super(props);

            this.onOverHandler = this.onOverHandler.bind(this);
            this.onDownHandler = this.onDownHandler.bind(this);
            this.onOutHandler = this.onOutHandler.bind(this);
            this.onUpHandler = this.onUpHandler.bind(this);
            this.onMoveHandler = this.onMoveHandler.bind(this);
            this.onClickHandler = this.onClickHandler.bind(this);
        }

        componentWillUnmount() {
            document.body.style.cursor = '';
        }

        render() {
            const {
                cursor, canDrag,
                onMouseOver, onMouseDown, onMouseOut, onMouseUp, onMouseMove, onClick,
                onDragStart, onDrag, onDragEnd,
                style:newStyle,          //高阶组件封装后，新组件接收的 style
                ...newProps              //过滤后的属性
            } = this.props;

            this.cursor = cursor!==undefined?cursor:this.cursor;
            this.canDrag = canDrag!==undefined?canDrag:this.canDrag;
            this.onMouseOver = onMouseOver;
            this.onMouseDown = onMouseDown;
            this.onMouseOut = onMouseOut;
            this.onMouseUp = onMouseUp;
            this.onMouseMove = onMouseMove;
            this.onClick = onClick;
            this.onDragStart = onDragStart;
            this.onDrag = onDrag;
            this.onDragEnd = onDragEnd;

            // 给被包裹组件注册指针事件
            newProps.onPointerOver = this.onOverHandler;
            newProps.onPointerDown = this.onDownHandler;
            newProps.onPointerOut = this.onOutHandler;
            newProps.onPointerUp = this.onUpHandler;
            newProps.onPointerMove = this.onMoveHandler;
            newProps.onClick = this.onClickHandler;

            const elementsTree = super.render();
            const {style:oldStyle, ...oldProps} = elementsTree.props;
            const mergeStyle = {...oldStyle,...newStyle};
            const {isOver} = this.state;
            if (isOver) {
                mergeStyle.cursor = this.cursor;
                document.body.style.cursor = this.canDrag ? this.cursor : '';
            } else {
                mergeStyle.cursor = '';
                document.body.style.cursor = '';
            }
            const mergeProps = Object.assign({}, oldProps, newProps);
            mergeProps.style = mergeStyle;

            // 后传入的 children 会覆盖只是的 children
            let children = oldProps.children;
            if(this.props.children){
                children = this.props.children;
            }
            return React.cloneElement(elementsTree, mergeProps, children);
        }

        onOverHandler(e) {
            this.onMouseOver && this.onMouseOver.call(this, e, this.getMousePosition(e));
            this.setState({
                ...this.state,
                isOver: true
            });
        }

        onDownHandler(e) {
            this.isDown = true;
            if (this.canDrag) {
                e.target.setPointerCapture(e.pointerId);
            }
            const position = this.getMousePosition(e);
            this.onMouseDown && this.onMouseDown.call(this, e, position);
            if(this.canDrag){
                this.onDragStart && this.onDragStart.call(this, e, position);
            }
        }

        onOutHandler(e) {
            const position = this.getMousePosition(e);
            this.onMouseOut && this.onMouseOut.call(this, e, position);
            this.setState({
                ...this.state,
                isOver: false,
            });
        }

        onUpHandler(e) {
            this.isDown = false;
            if (this.canDrag) {
                e.target.releasePointerCapture(e.pointerId);
            }
            const position = this.getMousePosition(e);
            this.onMouseUp && this.onMouseUp.call(this, e, position);
            if(this.canDrag){
                this.onDragEnd && this.onDragEnd.call(this, e, position);
            }
        }

        onMoveHandler(e) {
            const position = this.getMousePosition(e);
            this.onMouseMove && this.onMouseMove.call(this, e, position);
            if(this.canDrag && this.isDown){
                this.onDrag && this.onDrag.call(this, e, position);
            }
        }

        onClickHandler(e) {
            const position = this.getMousePosition(e);
            this.onClick && this.onClick.call(this, e, position);
        }

        getMousePosition(e) {
            switch (e.type) {
                case 'pointermove':
                    this.dx = e.pageX - this.globalX;
                    this.dy = e.pageY - this.globalY;
                    break;
                default:
                    this.dx = 0;
                    this.dy = 0;
                    break;
            }
            this.globalX = e.pageX;
            this.globalY = e.pageY;
            const dom = ReactDOM.findDOMNode(this);
            if (dom) {
                const rect = dom.getBoundingClientRect();
                this.localX = e.pageX - rect.x + document.documentElement.scrollLeft;
                this.localY = e.pageY - rect.y + document.documentElement.scrollTop;
            }

            return {
                localX: this.localX,
                localY: this.localY,
                globalX: this.globalX,
                globalY: this.globalY,
                dx: this.dx,
                dy: this.dy
            }
        }
    }
};

export default mouseHandler;